.button {
  position: relative;
  display: inline-block;
  font-family: "Gotu", sans-serif;
  font-size: 0.875rem;
  color: #fff;
  line-height: 1.5714;
  border: 0.0625rem solid #408f95;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  padding: 1rem 0;
  white-space: nowrap;
  z-index: 1;
  text-align: center;
  width: 100%;
  max-width: 12.875rem;
  background-color: #408f95;
}

.button::after {
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

.button:hover {
  color: #408f95;
}

.button:hover::after {
  transform: scale(1, 1);
}

.button::before {
  position: absolute;
  content: "";
  width: 100%;
  max-width: 12.875rem;
  height: 3.375rem;
  border: 0.0625rem solid #408f95;
  left: 0.25rem;
  top: 0.25rem;
  z-index: -1;
}

.button__arrow {
  position: relative;
  display: inline-block;
  width: 2.5rem;
  height: 0.0625rem;
  background-color: #fff;
  vertical-align: middle;
}

.button__arrow::after {
  position: absolute;
  content: "";
  height: 0.0625rem;
  width: 0.625rem;
  background-color: #fff;
  right: 0;
  top: 0;
  transform: rotate(45deg);
  transform-origin: top right;
}

.button:hover .button__arrow {
  background-color: #408f95;
}

.button:hover .button__arrow::after {
  background-color: #408f95;
}

